@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;600&display=swap');
/* Global Styles */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    color: #333;
    line-height: 1.6;
  }
  
  h1 {
    color: #004d7a;
  }
  
  p {
    max-width: 600px;
    margin: 10px auto;
    text-align: center;
  }
  
  a {
    color: #004d7a;
    font-family: 'Dancing Script', cursive; /* Girly font for navigation links */
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  /* Christmas Page Styles */
  .christmas {
    background: linear-gradient(to bottom, #004d7a, #ff7e5f);
    background-size: cover;
    background-attachment: fixed;
    overflow: hidden;
  }
  
  /* Snowflake Animation */
  .snowflake {
    position: absolute;
    top: -10px;
    font-size: 1rem;
    color: white;
    opacity: 0.8;
    animation: fall 5s linear infinite;
  }
  
  @keyframes fall {
    0% {
      transform: translateY(0);
      opacity: 1;
    }
    100% {
      transform: translateY(100vh);
      opacity: 0;
    }
  }